// All Color

$backgroundColor: #121212;
$backColor: #000000;
$mainTextColor: #9C9C9C;
$headerTextColor: #0d347c;
$headerGradientTextColor: #E5E5E5;
$extraColor: #7D94C2;


// All Font Size

$nameFontSize: 15vh;
$titleFontSize: 4vh;
$headerFontSize: 8vh;
$aboutTextFontSize: 2.6vh;
$futureTitleFontSize: 2vh;
$futureTextFontSize: 1.6vh;
$helloTextFontSize: 3.2vh;

//Variable

$boxWidth: 33vh;
$boxHeight: 50vh;
$marginTop: 12vh;
$marginCursorTop: 14vh;
$marginButton: 17vh;
$marginButtonTop: 6vh;
$marginTopText: 4.5vh;

// All Font Family

$headerFontFamily: 'Bangers';
$mainFontFamily: 'Dancing Script';
$titleFontFamil: 'Shadows Into Light';

@mixin flexColumn {
    display:flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: space-evenly;
}

@mixin flexRow {
    display:flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
}

@mixin header {
    text-align: center;
    background: linear-gradient($headerTextColor, $headerGradientTextColor);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    margin-top: 20vh;
    font-family: $headerFontFamily;
}

@mixin middle{
    margin-left: auto;
    margin-right: auto;
}

@mixin middle1 {     
    display: flex;
    justify-content: center;
    align-items: center;
}

@mixin text {
    margin-top: $headerTextFontSize;
    @include middle();
    color:$textColor;
    font-family: $textFontFamily;
}

@mixin mainBox(){
    text-align: justify !important;
    font-family: $textFontFamily;
    background-color: $bodyColor;
}

@mixin containerrClass {
    width: 100%;
	padding: 30px 90px 30px 30px;
	position: relative;
	border-radius: 0 500px 500px 0;
	text-align: center;
}

@mixin secondText{
    @include middle();
    @include flexRow();

    .titleText{
        color: $mainTextColor;
        font-family: $mainFontFamily;
        font-size: $titleFontSize;
    }

    .cvText{
        margin-top: $titleFontSize;
        border: 0.3vh solid $extraColor;
        padding: 0.5vh 5vh;
        border-radius: 3vh;
        font-family: $titleFontFamil;
        color: $mainTextColor !important;
        font-size: $aboutTextFontSize;
    }

    .cvText:hover{
        text-decoration: none;
        background-color: $extraColor;
        color: $backgroundColor !important;
    }
}

@mixin box{
    background-color: $backColor;
    width: $boxWidth;
    height: $boxHeight;
    border: 1vh solid $backColor;
    border-radius: 3vh;
    margin: 3vh;
    margin-top: $marginTop;
}

@mixin futureTitle{
    margin-top: $aboutTextFontSize;
    color: $mainTextColor;
    width: 100%;
    text-align: center;
    font-size: $futureTitleFontSize;
    font-family: $titleFontFamil !important;
    @include middle();
}

@mixin futureText {
    width: 85%;
    color: $mainTextColor;
    text-align: justify;
    font-size: $futureTextFontSize;
    font-family: $titleFontFamil;
    margin-top: $marginTopText;
    @include middle();
}

@mixin futureButton(){

    .linkButtons{
        background-color: $backgroundColor;
        border: 0.3vh solid $backgroundColor;
        border-radius: 3vh;
        padding: 0.3vh 3vh;
        color: $mainTextColor;
        font-family: $titleFontFamil;
        font-size: $futureTitleFontSize;
    }

    .linkButtons:hover{
        text-decoration: none;
        background-color: $extraColor;
        color: $backgroundColor !important;
        border: 0.3vh solid $extraColor;
    }
}