.mainBox{
    @include flexRow();

    .box{
        @include box();

        .image{
            width: 100%;
            height: 15vh;
            border-radius: 3vh;
            @include middle();
        }

        .futureTitle{
            @include futureTitle();
        }

        .futureText{
            @include futureText();
        }
    }
}

.buttons{
    @include middle();
    @include futureButton();
    margin-top: $marginButtonTop;
    text-align: center;
}

.special {
    @include flexRow();
    @include secondText();
    margin-top: 4vh;
}