.main{
    .headerText{
        @include header();
        font-size: $headerFontSize;
        @include flexColumn();
    }
}

.aboutText{
    margin-top: $marginTop;
    @include middle();
    width: 55%;
    color: $mainTextColor;
    font-family: $titleFontFamil;;
    font-size: $aboutTextFontSize;
    text-align: justify;
    text-transform: capitalize !important;
    letter-spacing: 0.1vh;

    .helloText{
        @include middle();
       text-align: center;
       font-size: $helloTextFontSize;
    }

}

@media only screen and (max-width: 920px) {
    .aboutText{
        width: 75% !important;
    }
}